@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: "Open Sans", sans-serif;
    font-size: 100%;
}

.header {
    display: flex;
}

.header__logo {
    text-decoration: none;
    text-transform: uppercase;
    font-family: "Open Sans", sans-serif;
    font-weight: bold;
    letter-spacing: 1px;
    font-size: 1.12rem;
    color: #000;
}

.header__ancor {
    display: inline-block;
    text-decoration: none;
    text-transform: uppercase;
    color: #fff;
    background-color: #006ef1;
    font-family: "Open Sans", sans-serif;
    font-weight: 700;
    font-size: 1.12rem;
    padding: 12px 40px;
    border-radius: 32px;
}

.header__burger {
    display: block;
    width: 35px;
    height: 35px;
    position: relative;
}

.header__burger span,
.header__burger span::before,
.header__burger span::after {
    position: absolute;
    top: 50%;
    margin-top: -1.5px;
    left: 50%;
    margin-left: -13px;
    height: 3px;
    width: 26px;
    background-color: #000;
}

.header__burger span::before,
.header__burger span::after {
    content: "";
}

.header__burger span::before {
    -webkit-transform: translateY(-7px);
    transform: translateY(-7px);
}

.header__burger span::after {
    -webkit-transform: translateY(7px);
    transform: translateY(7px);
}

.nav__list {
    display: flex;
    justify-content: space-between;
    list-style: none;
}

.nav__link {
    display: block;
    text-decoration: none;
    text-transform: capitalize;
    padding: 0 20px;
    color: #000;
    font-family: "Open Sans", sans-serif;
    font-weight: 500;
}

.main-content {
    background-image: url(/static/media/main_bg.df5789fd.webp);
    position: relative;
}

.main-content::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #006ef1;
    z-index: -1;
}

.main-content__head {
    display: flex;
    flex-wrap: wrap;
}

.main-content__title {
    margin-right: 90px;
    color: #fff;
}

.main-content__subtitle {
    color: #fff;
}

.main-content__body {
    display: flex;
    flex-wrap: wrap;
}

.main-content__form {
    display: flex;
    flex-direction: column;
}

.main-content__mail,
.main-content__name,
.main-content__message {
    width: 100%;
    background-color: #0056dd;
    border: none;
    border-radius: 8px;
    color: #fff;
    outline: #0056dd;
}

.main-content__mail:focus,
.main-content__name:focus,
.main-content__message:focus {
    background-color: rgba(255, 255, 255, .2);
}

.main-content__mail::-webkit-input-placeholder,
.main-content__name::-webkit-input-placeholder,
.main-content__message::-webkit-input-placeholder {
    color: #6496e8;
}

.main-content__mail::-moz-placeholder,
.main-content__name::-moz-placeholder,
.main-content__message::-moz-placeholder {
    color: #6496e8;
}

.main-content__mail:-ms-input-placeholder,
.main-content__name:-ms-input-placeholder,
.main-content__message:-ms-input-placeholder {
    color: #6496e8;
}

.main-content__mail::-ms-input-placeholder,
.main-content__name::-ms-input-placeholder,
.main-content__message::-ms-input-placeholder {
    color: #6496e8;
}

.main-content__mail::-webkit-input-placeholder, .main-content__name::-webkit-input-placeholder, .main-content__message::-webkit-input-placeholder {
    color: #6496e8;
}

.main-content__mail::-moz-placeholder, .main-content__name::-moz-placeholder, .main-content__message::-moz-placeholder {
    color: #6496e8;
}

.main-content__mail:-ms-input-placeholder, .main-content__name:-ms-input-placeholder, .main-content__message:-ms-input-placeholder {
    color: #6496e8;
}

.main-content__mail::-ms-input-placeholder, .main-content__name::-ms-input-placeholder, .main-content__message::-ms-input-placeholder {
    color: #6496e8;
}

.main-content__mail::placeholder,
.main-content__name::placeholder,
.main-content__message::placeholder {
    color: #6496e8;
}

.main-content__message {
    resize: none;
}

.main-content__submit {
    display: inline-block;
    padding: 15px 80px;
    font-weight: bold;
    font-size: 1.2rem;
    text-transform: uppercase;
    color: #fff;
    background-color: #000;
    border: none;
    border-radius: 32px;
    cursor: pointer;
}

.main-content__submit:hover {
    box-shadow: 0 10px 20px rgba(0, 0, 0, .15);
}

.main-content__text-content {
    background-color: #fff;
}

.main-content__text-title {
    margin-bottom: 15px;
}

.main-content__text {
    margin-bottom: 30px;
    line-height: 1.7;
}

.main-content__text:last-child {
    margin-bottom: 0;
}

.main-content__visit {
    display: flex;
    width: 100%;
}

.footer {
    background-color: #142333;
}

.footer__head {
    display: flex;
    justify-content: space-between;
    position: relative;
}

.footer__text {
    color: #707e8c;
    margin-bottom: 30px;
    line-height: 1.6;
}

.footer__text:last-child {
    margin-bottom: 0;
}

.footer__title {
    color: #707e8c;
    margin-bottom: 15px;
}

.footer__social {
    list-style: none;
    display: flex;
    justify-content: space-between;
}

.footer__social-link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background-color: rgba(112, 126, 140, .15);
}

.footer__wrap {
    display: flex;
    justify-content: space-between;
}

.footer__bottom {
    padding: 30px 0;
    background-color: #202f40;
}

.footer .nav__link {
    color: #707e8c;
    font-size: 1.1rem;
}

.footer__copyright {
    color: #707e8c;
}

@media screen and (min-width: 320px) {
    .container {
        margin: 0;
    }
    .header {
        margin: 0 auto;
        padding: 10px 20px;
        justify-content: space-between;
        align-items: center;
    }
    .header .nav,
    .header__ancor {
        display: none;
    }
    .main-content {
        background-size: contain;
        padding: 0;
        padding-top: 20px;
    }
    .main-content__head {
        flex-direction: column;
        margin-bottom: 15px;
        padding: 0 20px;
    }
    .main-content__title {
        font-size: 1.4rem;
    }
    .main-content__subtitle {
        font-size: .8rem;
    }
    .main-content__body {
        padding: 0;
        align-items: center;
    }
    .main-content__form {
        align-items: center;
        width: 100%;
        padding: 0 20px 15px;
    }
    .main-content__name,
    .main-content__mail,
    .main-content__message {
        font-size: 16px;
        padding: 20px;
        margin-bottom: 15px;
    }
    .main-content__message {
        min-height: 90px;
    }
    .main-content__submit {
        align-self: center;
    }
    .main-content__text-content {
        border-radius: 0;
        padding: 20px;
    }
    .main-content__visit {
        padding: 20px;
        flex-direction: column;
        position: static;
        border-radius: 0;
        width: 100%;
        background-color: #f3f6fa;
    }
    .main-content__visit-title {
        margin-bottom: 15px;
    }
    .main-content__image {
        display: none;
    }
    .footer {
        padding-top: 25px;
    }
    .footer .container {
        padding: 0 20px;
    }
    .footer__head {
        flex-wrap: wrap;
        padding-bottom: 0;
    }
    .footer__head-text {
        margin-bottom: 30px;
    }
    .footer__text {
        line-height: 1.4;
    }
    .footer__contacts {
        display: flex;
        flex-direction: column;
        width: 100%;
    }
    .footer__social {
        width: 100%;
        margin-bottom: 30px;
    }
    .footer .nav__list {
        flex-direction: column;
        padding-bottom: 20px;
    }
    .footer .nav__item {
        margin-bottom: 20px;
    }
    .footer .nav__item:last-child {
        margin: 0;
    }
    .footer .nav__link {
        font-size: 1rem;
    }
    .footer__mail-link {
        position: static;
        align-self: flex-end;
        font-size: 0;
    }
    .footer__wrap {
        flex-direction: column;
    }
    .footer__copyright {
        padding: 0 20px;
        font-size: 12px;
    }
}

@media screen and (min-width: 440px) {
    .main-content__head {
        flex-direction: row;
    }
    .main-content__text-content {
        width: 100%;
    }
}

.footer__social {
    width: 240px;
    margin: 0 auto;
    margin-bottom: 30px;
}

@media screen and (min-width: 700px) {
    .header__mobile {
        display: none;
    }
    .header .nav {
        display: block;
    }
    .footer .nav__list {
        flex-direction: row;
    }
}

@media screen and (min-width: 900px) {
    .header {
        justify-content: space-between;
        align-items: center;
        padding: 15px 15px;
    }
    .header .container {
        width: 100%;
        max-width: 1120px;
        margin: 0 auto;
    }
    .header__ancor {
        display: block;
    }
    .main-content .container {
        margin: 0 auto;
    }
    .main-content__body {
        padding-right: 20px;
    }
    .main-content__form {
        width: 400px;
    }
    .main-content__text-content {
        width: 50%;
        align-self: flex-start;
        border-radius: 8px;
    }
    .main-content__visit {
        flex-direction: row;
    }
    .main-content__visit-title {
        margin-right: 50px;
        min-width: 100px;
    }
    .footer__head {
        flex-wrap: nowrap;
    }
    .footer__contacts {
        width: auto;
    }
    .footer__wrap {
        flex-direction: row;
        align-items: center;
    }
    .footer .nav__list {
        padding-bottom: 0;
    }
    .footer .nav__item {
        margin-bottom: 0;
    }
}

@media screen and (min-width: 1000px) {
    .main-content {
        background-position: right top -80px;
        padding: 100px 0 200px;
    }
    .main-content__head {
        margin-bottom: 50px;
        padding: 0 20px;
    }
    .main-content__title {
        font-size: 2rem;
    }
    .main-content__subtitle {
        font-size: 1.4rem;
    }
    .main-content__body {
        justify-content: space-between;
        padding: 0 20px;
    }
    .main-content__form {
        width: 450px;
        padding: 0;
    }
    .main-content__mail,
    .main-content__name,
    .main-content__message {
        font-size: 18px;
        margin-bottom: 20px;
        padding: 20px;
    }
    .main-content__message {
        min-height: 120px;
    }
    .main-content__submit {
        align-self: start;
    }
    .main-content__text-content {
        width: 450px;
        padding: 35px;
    }
    .main-content__text-title {
        font-size: 1.6rem;
        margin-bottom: 15px;
    }
    .main-content__image {
        display: inline-block;
        position: absolute;
        bottom: 100%;
        right: 180px;
    }
    .main-content__visit {
        max-width: 1120px;
        position: absolute;
        padding: 45px;
        left: 50%;
        -webkit-transform: translateX(-50%);
        transform: translateX(-50%);
        bottom: -50px;
        background-color: #fff;
        border-radius: 8px;
    }
    .main-content__visit-title {
        margin-right: 150px;
        font-size: 1.5rem;
    }
    .main-content__visit-text {
        max-width: 640px;
    }
    .footer {
        padding-top: 100px;
    }
    .footer .container {
        padding: 0;
    }
    .footer__head {
        padding: 0 20px;
        padding-bottom: 50px;
    }
    .footer__head-text {
        width: 600px;
    }
    .footer__mail-link {
        display: inline-block;
        position: absolute;
        bottom: -20px;
        right: 0;
    }
    .footer__wrap {
        max-width: 1120px;
        margin: 0 auto;
    }
    .footer__copyright {
        font-size: 1.1rem;
    }
}

@media screen and (min-width: 1200px) {
    .container {
        width: 100%;
        max-width: 1120px;
        margin: 0 auto;
    }
    .header {
        padding: 15px 0;
    }
    .main-content__head {
        padding-left: 0;
    }
    .main-content__body {
        padding-right: 100px;
        padding-left: 0;
    }
    .footer__head {
        padding: 0;
    }
}
